import React, { Component } from 'react';

import Overview from './Components/Navigation/Overview';

const CLIENT_ID = '523150784546-0oc3p34t4fo0iha2jv2tv55ke2ddj5m2.apps.googleusercontent.com';
const SCOPES = 'https://www.googleapis.com/auth/spreadsheets.readonly';

export default class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      authenticated: false
    };

    this.authenticate = this.authenticate.bind(this);
    this.checkAuth = this.checkAuth.bind(this);
  }

  componentDidMount() {
    window.gapi.load('client', () => {
      this.checkAuth(true, this.handleAuth.bind(this));
    });
  }

  authenticate(e) {
    e.preventDefault();
    this.checkAuth(false, this.handleAuth.bind(this));
  }

  checkAuth(immediate, callback) {
    window.gapi.auth.authorize(
      {
        client_id: CLIENT_ID,
        scope: SCOPES,
        immediate
      },
      callback
    );
  }

  handleAuth(authResult) {
    if (authResult && !authResult.error) {
      this.setState({
        authenticated: true
      });
    } else {
      this.setState({
        authenticated: false
      });
    }
  }

  render() {
    return (
      <div>
        {this.state.authenticated ? (
          <Overview />
        ) : (
          <button onClick={this.authenticate}>Login</button>
        )}
      </div>
    );
  }
}



