import React from 'react';
// We don't use Router, but somehow it needs to be in scope to get Link working.
// eslint-disable-next-line
import { BrowserRouter as Router, Link } from 'react-router-dom';
import loadPartnersfromSheet from '../../Google/utils/loadPartnersFromSheet';

/**
 * @param { String } uri - Where the url should direct to
 * @returns { Link } - A valid react-router Link
 */
function createReactRouterLink(uri) {
  const endpoint = `/${uri}`;
  const url = `athletia.net/reports${endpoint}`;
  return <Link to={endpoint}>{url}</Link>;
}

/**
 * @param { Array } partners - The partners that will appear in the partner table and
 * for which we need a report.
 * @returns { Array } - A copy of partners, but instead of an url string we now have
 * a react-router Link.
 */
export function getPartners(callback, errorCallback) {
  loadPartnersfromSheet(partners => {
    const partnersWithUrl = partners.map(partner => {
      return {
        ...partner,
        url: createReactRouterLink(partner.uri)
      };
    });

    callback(partnersWithUrl);
  }, errorCallback);
}

export default { getPartners };



