import React, { Component } from 'react';
import Loader from '../Misc/Loader';

import getIframeByUri from './utils/iframe';

export default class Dashboard extends Component {
  constructor(props) {
    super(props);
    this.state = { iframe: '', loading: false };
  }

  componentWillMount() {
    this.setState({ loading: true });
    const { partners } = this.props;
    const { path } = this.props.match;
    const uri = path.replace('/reports/', '');

    getIframeByUri(partners, uri, iframe => {
      this.setState({ iframe, loading: false });
    });
  }

  render() {
    return <div>{this.state.loading ? <Loader /> : this.state.iframe}</div>;
  }
}



