import React from 'react';

function getIframes(partners) {
  if (partners.length < 1) return null;

  return partners.map(partner => {
    return {
      ...partner,
      iframe: (
        <div
          style={{ textAlign: 'center', backgroundColor: 'black' }}
          /* eslint-disable react/no-danger */
          dangerouslySetInnerHTML={{ __html: partner.iframe }}
        />
      )
    };
  });
}

export default function getIframeByUri(partners, uri = '', callback) {
  const iframes = getIframes(partners);
  // get the specific iframe for the given uri
  const specificIframes = iframes.filter(
    partner => `/${partner.uri}` === uri || partner.uri === uri
  )[0].iframe;

  callback(specificIframes);
}



