import React, { Component } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';

// import Partner from '../Partners/Partner';
import Dashboard from '../Partners/Dashboard';
// import NoMatch from './NoMatch';
import Loader from '../Misc/Loader';
import Error from '../Misc/Error';

import { getPartners } from '../Partners/utils/partners';

const TestComponent = () => <p>You are welcome!</p>;

export default class Overview extends Component {
  constructor(props) {
    super(props);

    this.state = { partners: [], loading: false };
  }

  componentWillMount() {
    this.setState({ loading: true });
    getPartners(
      partners => this.setState({ partners, loading: false }),
      error => this.setState({ error, loading: false })
    );
  }

  Routing() {
    if (this.state.loading) {
      return <Loader />;
    }
    if (this.state.error) {
      const { message } = this.state.error.result.error;
      return (
        <Error>
          <p>{message}</p>
        </Error>
      );
    }
    return (
      <Router>
        <div>
          <Route path="/" component={TestComponent} />
          {/* <Route
            exact
            path="/overview"
            render={props => <Partner {...props} partners={this.state.partners} />}
          /> */}
          {this.state.partners.map(partner => {
            return (
              <Route
                key={partner.uri}
                path={`/${partner.uri}`}
                render={props => <Dashboard {...props} partners={this.state.partners} />}
              />
            );
          })}
        </div>
      </Router>
    );
  }

  render() {
    return this.Routing();
  }
}



